@import url(https://fonts.googleapis.com/css?family=Cinzel);
@-webkit-keyframes windows {
  1% {
    fill: #000000; }
  50% {
    fill: #FEE600; }
  100% {
    fill: #000000; } }

@keyframes windows {
  1% {
    fill: #000000; }
  50% {
    fill: #FEE600; }
  100% {
    fill: #000000; } }

@-webkit-keyframes moveClouds {
  100% {
    -webkit-transform: translateX(300px);
            transform: translateX(300px); } }

@keyframes moveClouds {
  100% {
    -webkit-transform: translateX(300px);
            transform: translateX(300px); } }

@-webkit-keyframes moveFlags {
  0% {
    -webkit-transform: rotateX(0deg);
            transform: rotateX(0deg); }
  100% {
    -webkit-transform: rotateX(12deg);
            transform: rotateX(12deg); } }

@keyframes moveFlags {
  0% {
    -webkit-transform: rotateX(0deg);
            transform: rotateX(0deg); }
  100% {
    -webkit-transform: rotateX(12deg);
            transform: rotateX(12deg); } }

.windowOdd {
  -webkit-animation: windows 5s alternate infinite;
          animation: windows 5s alternate infinite; }

.windowEven {
  animation: windows 5s alternate-reverse infinite; }

.clouds {
  -webkit-animation: moveClouds 60s alternate infinite;
          animation: moveClouds 60s alternate infinite; }

.flags {
  -webkit-animation: moveFlags 1s alternate infinite;
          animation: moveFlags 1s alternate infinite; }

html,
body {
  padding: 0;
  margin: 0;
  background-color: #809299; }

.App .intro-page {
  position: fixed; }
  .App .intro-page:focus .svgBtn, .App .intro-page :hover .svgBtn {
    fill: #EEB95C; }
  .App .intro-page:focus .svgBtnText, .App .intro-page :hover .svgBtnText {
    fill: #000000; }

@-webkit-keyframes moveFlames {
  100% {
    -webkit-transform: translateY(2px);
            transform: translateY(2px); } }

@keyframes moveFlames {
  100% {
    -webkit-transform: translateY(2px);
            transform: translateY(2px); } }

.playerSelector {
  position: fixed; }
  .playerSelector .playerSelectorBtn {
    cursor: pointer;
    background: none;
    border: none; }
    .playerSelector .playerSelectorBtn:focus, .playerSelector .playerSelectorBtn :hover {
      outline: none; }
      .playerSelector .playerSelectorBtn:focus .svgBtn, .playerSelector .playerSelectorBtn :hover .svgBtn {
        fill: #EEB95C; }
      .playerSelector .playerSelectorBtn:focus .svgBtnText, .playerSelector .playerSelectorBtn :hover .svgBtnText {
        fill: #000000; }
    .playerSelector .playerSelectorBtn:disabled {
      cursor: no-drop; }
      .playerSelector .playerSelectorBtn:disabled .svgBtn {
        opacity: .5;
        fill: #000000; }
      .playerSelector .playerSelectorBtn:disabled .svgBtnText {
        fill: gray; }
  .playerSelector .playerInput {
    width: 100%;
    height: 100%;
    padding: 0 10px;
    font-size: 20px;
    opacity: 0.6; }
  .playerSelector .flames {
    -webkit-animation: moveFlames 0.5s alternate infinite;
            animation: moveFlames 0.5s alternate infinite; }

.match {
  position: fixed; }
  .match .move:hover {
    cursor: pointer; }
    .match .move:hover .background {
      fill: #FDCFAF; }

.resultPage {
  position: fixed; }
  .resultPage:focus .svgBtn,
  .resultPage :hover .svgBtn {
    fill: #eeb95c; }
  .resultPage:focus .svgBtnText,
  .resultPage :hover .svgBtnText {
    fill: #000000; }

.statisticsPage {
  font-family: 'Cinzel', serif;
  font-weight: bold;
  text-align: center; }
  .statisticsPage .title {
    font-size: 70px;
    color: white; }
  .statisticsPage ul {
    padding: 0; }
    .statisticsPage ul li {
      padding: 0;
      list-style: none;
      text-align: center;
      background-color: #B34D22;
      font-size: 40px; }

